import topheader from '@/components/chat/topheader'
import _ from "underscore";
import f from "@/application/functions";

import contacts from '@/components/contacts/index.vue'
import userspic from '@/components/assets/userspic'
import {mapState} from "vuex";
import members from '@/components/chatInfo/info/members/index.vue'
import images from '@/components/chatInfo/info/images/index.vue'
import files from '@/components/chatInfo/info/files/index.vue'

import avatarsList from '@/components/chatInfo/assets/avatarsList/index.vue'
import chatName from '@/components/chats/assets/name.vue'
import chatIcon from '@/components/chats/assets/icon.vue'
import {push} from "underscore/modules/_setup";

export default {
  data: function() {
    return {
      usr: {},
      ind: 0,
      roomMuted: false,
      encryptedEvents: [],
      imageEvents: [],
      pics: false,
      fileEvents: [],
      inputActive: false,
      menuItems: [
        {
          click: "adminMakeModer",
          title: this.$i18n.t("caption.makeModerator"),
          icon: "fas fa-user-shield",

        },

        {
          click: "adminBan",
          title: this.$i18n.t("caption.ban"),
          icon: "fas fa-ban",

          upload: {
            multiple: true
          },
        },
        {
          click: "adminKick",
          title: this.$i18n.t("caption.kick"),
          icon: "fas fa-user-times"
        }
      ],
      menuItemsModer: [
        {
          click: "moderBan",
          title: this.$i18n.t("caption.ban"),
          icon: "fas fa-ban"
        },
        {
          click: "moderKick",
          title: this.$i18n.t("caption.kick"),
          icon: "fas fa-user-times"
        }
      ],
      admin: String,
      imagesList: [],
      localRoomName: '',
      humanSize: Number,
      inviteUserOpened: false,
      msList: [],
      isOpen: false,
      actionMemberShow: false,
      options: {
        index: 0,
        arrowEl: false,
        fullscreenEl: false,
        shareEl: false
      },
      roomName: '',
      nameEdit: false,
      accordionList: [
        {title: this.$i18n.t("caption.members")},
        {title: this.$i18n.t("caption.media")},
        {title: this.$i18n.t("caption.files")}
      ],
      isActive: null,
      isActiveAction: null,
      currentUser: '',
      actionUser: {},
      actionUserMenu: false,
      membersCount: Number,
      chatMembers: [],
      blocked: false
    }

  },
  props: {
    chat: {}
  },
  components: {
    images,
    members,
    files,
    topheader,
    avatarsList,

    userspic,
    contacts,
    chatName,
    chatIcon
  },
  
  watch: {
    m_chat : {
      immediate: true,
      handler: function () {

        if(this.m_chat && !_.isEmpty(this.m_chat)){

          this.core.pcrypto.addroom(this.m_chat).then(r => {

            this.ready = true

            this.encrypted = this.m_chat.pcrypto.canBeEncrypt()

          })

        }
       
      }
    },
    chat: {
      immediate: true,
      handler: function () {
        if (this.chat){
          this.$store.commit('SET_CURRENT_ROOM', this.chat.roomId)
        }
        else
          this.$store.commit('SET_CURRENT_ROOM', false)
      }
    }
  },

  computed: {
    ...mapState([
      'minimized',
      'active'
    ]),
    events: function () {
      var pushRules = this.core.mtrx.client._pushProcessor.getPushRuleById(this.chat.roomId)
      if (pushRules !== null) {
        this.roomMuted = true
      }
      return this.m_chat.timeline || {}

    },
    customRoomType: function () {
      var type = this.core.mtrx.customRoomType(this.chat.roomId)


      return type
    },
    blockedCheck: function () {
      var self = this
      var mId = f.getmatrixid(this.core.mtrx.client.credentials.userId)
      var user = _.map(self.chatMembers, function (member) {
        if (member.id !== mId) {
          return member.id
        }
      }).filter(n => n)
      var blackList = this.core.mtrx.client.getIgnoredUsers()
      return blackList.includes(user[0])

    },
    users: function () {


      var users = _.filter(_.map(this.chat?.getJoinedMembers() || [], (m) => {

        return this.$f.deep(this, '$store.state.users.' + m.name) || null

      }), (m) => {
        return m && m.id != this.core.user.userinfo?.id
      })

      return users
    },
    m_chat: function () {

      if (this.chat && this.chat.roomId) {
        var m_chat = this.core.mtrx.client.getRoom(this.chat.roomId)
        this.membersCount = m_chat.currentState.getMembers().length
        return m_chat || {}
      }
    },

    galleryImages: function () {
      var imgArr = []
      this.imageEvents.forEach(function (event) {
        if (event.event.content.msgtype === 'm.image') {
          imgArr.push({
            src: event.event.content.url,
            w: event.event.content.info.w,
            h: event.event.content.info.h
          })
        }
      })
      return imgArr
    },
    userPics: function () {
      return this.membersList.length > 4;
    },
    membersList: function () {
      var self = this
      var test = []
      var members = []
      var pNetUsers = this.$store.state.users
      this.currentUser = this.core.mtrx.client.credentials.userId
      // console.log(this.chat.members, "this chat from room info")

      if (this.m_chat._selfMembership === 'join') {
        self.chatMembers = _.map(self.chat.members, function (member) {
          var userInfo = {}
          _.map(pNetUsers, function (user) {
            if (member.name === user.id) {
              return userInfo = {
                id: user.id,
                image: user.image,
                name: user.name,
                level: member.powerLevel,
                roomStatus: member.membership,
                blocked: self.core.mtrx.client.isUserIgnored(member.name)
              }
            }
          })
          return userInfo
        })
      }

      if (this.m_chat._selfMembership === 'invite') {
        var names = _.map(self.chat.members, member => member.name)
        // console.log(self.chat.members, "members")
        // console.log(this.m_chat, "chat")
        this.core.user.usersInfo(names).then(r => {
          self.chatMembers = _.map(self.chat.members, function (member) {
            var userInfo = {}
            _.map(r, function (user) {
              if (member.name === user.id) {
                return userInfo = {
                  id: user.id,
                  image: user.image,
                  name: user.name,
                  level: 0,
                  status: member.status,
                  source: user.source
                }
              }
            })
            return userInfo
          })
        })

      }
      return this.chatMembers
    },
    memberRole: function () {
      var me = this.currentUser
      var role = ''
      _.map(this.membersList, function (user) {
        if (me === user.id && user.level === 100) {
          return role = 'Admin'
        }
        if (me === user.id && user.level === 50) {
          return role = 'Moderator'
        }
        if (me === user.id && user.level === 0) {
          return role = 'Member'
        }
      })
      return role
    },
    changeRoomName: function () {
      var roomName = this.m_chat.name
      return roomName.replace('_', ' ')
    }

  },
  mounted() {
    // this.changeName()
    this.eventsList()
  },
  methods: {
    banForgetRoom() {
      var banUserId = ''
      var my = this.m_chat.myUserId
      var members = this.m_chat.currentState.getMembers()
      members.forEach(function (member) {
        if (member !== my) {
          banUserId = member.userId
        }
      })
      this.core.mtrx.client.ban(this.chat.roomId, banUserId, 'leave').then(r => {
        return r
      })
    },
    muteRoom() {
      var roomId = this.chat.roomId
      var deviceID = this.core.mtrx.client.deviceId
      var self = this
      if (this.roomMuted) {
        self.core.mtrx.client.deletePushRule('global', 'room', roomId)
        self.roomMuted = false
      } else {
        self.core.mtrx.client.setRoomMutePushRule('global', roomId, 'true')
        self.roomMuted = true
      }


    },
    blockUser() {
      var self = this
      var blackList = this.core.mtrx.client.getIgnoredUsers()
      console.log(blackList, "before set");
      var mId = f.getmatrixid(this.core.mtrx.client.credentials.userId)
      var user = _.map(self.chatMembers, function (member) {
        if (member.id !== mId) {
          return member.id
        }
      }).filter(n => n)
      if (blackList.indexOf(user[0]) === -1) {
        blackList.push(user[0]);
        this.core.mtrx.client.setIgnoredUsers(blackList).then(r => {
          console.log(this.core.mtrx.client.getIgnoredUsers(), "set ignored users")
          return r
        })
      }
      this.blocked = true
    },
    unblock() {
      var self = this
      var blackList = this.core.mtrx.client.getIgnoredUsers()
      var mId = f.getmatrixid(this.core.mtrx.client.credentials.userId)
      var user = _.map(self.chatMembers, function (member) {
        if (member.id !== mId) {
          return member.id
        }
      }).filter(n => n)
      blackList.splice(blackList.indexOf(user[0]), 1)
      this.core.mtrx.client.setIgnoredUsers(blackList).then(r => {
        console.log(this.core.mtrx.client.getIgnoredUsers(), "removed user from block")
        return r
      })
      this.blocked = false


    },

    memberMenu(data) {
      this.usr = data.user
      this.ind = data.index
      this.menuItemClickHandler()
    },

    eventsList() {

      if (typeof this.events.filter == 'function') {

        this.imageEvents = this.events.filter(event => event.event.content.msgtype === 'm.image' || event.event.type === 'm.room.encrypted' && event.getClearContent().msgtype === 'm.image')
        this.fileEvents = this.events.filter(event => event.event.content.msgtype === 'm.file')

      }
    },

    jsonConvertor(data) {
      return JSON.parse(data)
    },

    toggleItem(index) {
      this.isActive = this.isActive === index ? null : index;
      this.actionMemberShow = null
    },

    menuItemClickHandler(item, rowObject) {
      if (item.click === 'adminMakeModer') {
        this.makeAdmin(rowObject.user)
        this.$refs.dropdownMenu.filter((e) => {
          e.hidePopup()
        })
      }

      if (item.click === 'adminBan') {
        this.$refs.dropdownMenu.filter((e) => {
          e.hidePopup()
        })
      }

      if (item.click === 'adminKick') {
        this.$refs.dropdownMenu.filter((e) => {
          e.hidePopup()
        })
      }

      if (item.click === 'moderBan') {
        this.$refs.dropdownMenu.filter((e) => {
          e.hidePopup()
        })
      }

      if (item.click === 'moderKick') {
        this.$refs.dropdownMenu.filter((e) => {
          e.hidePopup()
        })
      }

      this.actionUser = rowObject
    },
    forgetRoom() {
      this.core.mtrx.client.leave(this.chat.roomId).then(r => {
        this.core.mtrx.client.forget(this.chat.roomId, true).then(r => {
          return r
        }).then(r => {
          this.$store.commit('DELETE_ROOM', this.chat.roomId);
          console.log(this.$store.state.chats, "chats new")
          this.$router.push({path: '/chats'})
        })
      })


    },
    changeName() {
      this.localRoomName = this.m_chat.name
      this.localRoomName.replace('_', ' ')
    },
    saveRoomName() {
      this.nameEdit = false
      this.inputActive = false
      this.roomName.replace(" ", "_")
      this.core.mtrx.client.setRoomName(this.m_chat.roomId, this.roomName)
      // this.core.mtrx.client.setRoomName(this.chat.roomId)

    },
    makeAdmin(user) {
      var event = this.m_chat.currentState.getStateEvents("m.room.power_levels")
      this.core.mtrx.client.setPowerLevel(this.m_chat.roomId, user, 50, event[0]).then(r => {
      })

    },
    showEditRoomName() {
      this.nameEdit = true
      this.roomName = this.m_chat.name
      this.inputActive = true

    },
    editRoomName() {
    },

    memberAction(user) {
      this.actionMemberShow = !this.actionMemberShow
      this.actionUser = user
      this.isActiveAction = this.isActiveAction === user.index ? null : user.index;

    },

    kickUser(user) {
      this.core.mtrx.client.kick(this.m_chat.roomId, user).then(this.$nextTick(function () {
      }))
    },
    showPhotoSwipe(index) {
      this.isOpen = true
      this.$set(this.options, 'index', index)
    },
    accordion: function (event) {

      //Раскомментировать, если нужно, чтобы закрывались остальные пункты при открытии текущего
      // 	var matches = document.querySelectorAll('.accordion-item-head');
      //  	for(var i = 0; i < matches.length; i++) {
      //  		matches[i].classList.remove('active');
      //	 }

      event.target.classList.toggle('active');
    },

    hidePhotoSwipe() {
      this.isOpen = false
    },
    focus() {
      return $event.target.select()
    },
    showUserAbout(user) {
      this.$emit('showAbout', user)
    },

    getClick(room) {
      this.$emit('showRoomInfo', room)
    },

    getJoinedMembers() {
      var pNetUsers = this.$store.state.users
      var users = []
      var membersId = []
      var jMembers = []
      var adminId = ''
      var moderatorId = ''
      return this.core.mtrx.client.getJoinedRoomMembers(this.chat.roomId).then(members => {

        _.map(pNetUsers, function (id) {
          users.push(id)
        })
        _.map(members.joined, function (id) {
          membersId.push({id: id.display_name})
        })


        jMembers = f.filterByUserId(membersId, users)
        _.mapObject(this.chat.currentState.members, function (val, key) {
          if (val.powerLevel === 100) {
            adminId = f.getmatrixid(key)
          }
          if (val.powerLevel === 50) {
            moderatorId = f.getmatrixid(key)
          }
        })

        _.map(jMembers, function (member) {
          if (member.id === adminId) {
            member.role = 'owner'
          }
          if (member.id === moderatorId) {
            member.role = 'moderator'
          }
        })
        return jMembers
      }).then(e => {
        return e
      })


    },

    modalInviteUser() {
      this.inviteUserOpened = true
    },

    closeModal() {
      this.inviteUserOpened = false
    },

    closeContactModal(value) {
      this.inviteUserOpened = value
    }
  }
}